header {
  height: 80vh;
  overflow: hidden;
}
.h_100{
  height: 100%;
}
.header__container {
  height: 100%;
  position: relative;
}

.cta {
  display: flex;
  gap: 1.2rem;
  margin-top: 2.5rem;
  padding-right: 12px;
}
.container_left{
  width: 60%;
}
.container_right{
  width: 35%;
}

@media(max-width:1399px) {
  .container_left,.container_right{
    width: 100%;
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  .row_column{
    flex-direction: column;
  }
  header{
    height: 100%;
  }
}
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  right: 1rem;
    bottom: 1rem;
  z-index: 1;
}

.header__socials::after {
  content: '';
  width: 1px;
  height: 2rem;
  background: #343030;
}

.scroll__down {
  position: absolute;
  right: 5rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}
.row{
  display: flex;
  align-items: center;
  justify-content: center;
}
.a_social{
  font-size: 2rem;
  color: #343030;
}
@media screen and (max-width: 1024px){
  header {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }

  section {
    padding-top: 6rem;
  }
}

@media screen and (max-width: 600px){

  .header__socials,
  .scroll__down {
    display: none;
  }
}
.text_anim {
  animation: type 4s steps(60, end);
  /* Chrome and Safari Optimisation */
  -webkit-animation: type 4s steps(60, end) infinite;
  /* Firefox Optimisation */
  -moz-animation: type 4s steps(60, end) infinite;
  /* Opera Optimisation */
  o-animation: type 4s steps(60, end) infinite;
  white-space: nowrap;
  overflow: hidden;
  width: 4em;
}

/* Remaining CSS3 Animation */

@keyframes type {
    from { width: 0; }
}

/* Chrome and Safari Optimisation */
@-webkit-keyframes type {
    from { width: 0; }
}

/* Firefox Optimisation */
@-moz-keyframes type {
    from { width: 0; }
}

/* Opera Optimisation */
@-o-keyframes type {
    from { width: 0; }
}
